<template>
    <p-data-table
        :value="thirdPartyData"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
        dataKey="dId"
        v-model:selection="selectedRow"
        selectionMode="single"
        @row-select="onRowSelect"
    >
        <h3 style="padding: 5px 0px 5px 10px">Third Party Donations</h3>
        <p-column-group type="header">
            <p-row>
                <p-column
                    headerStyle="width: 3em; max-width: 3rem"
                    style="width: 3em; max-width: 3rem"
                >
                    <template #header>
                        <p-checkbox
                            v-model="selectAllCheckbox"
                            :binary="true"
                            @change="selectAllOnChange"
                        />
                    </template>
                </p-column>

                <p-column header="Donar ID" />
                <p-column header="Donar Name" />
                <p-column header="Donar DOB" />
                <p-column header="Donar Patient Doctor" />
                <p-column header="Cycle ID" />
                <p-column header="Recepient ID" />
                <p-column header="Recepient Name" />
                <p-column header="Recepient DOB" />
                <p-column header="Recepient Patient Doctor" />
                <p-column header="Date" />
                <p-column header="Number" />
                <p-column header="Donation Type" />
            </p-row>
        </p-column-group>

        <p-column
            headerStyle="width: 3em; max-width: 3rem"
            style="width: 3em; max-width: 3rem"
        >
            <template #body="{data}">
                <p-checkbox
                    v-model="checkList"
                    :name="data.id"
                    :value="data.id"
                />
            </template>
        </p-column>

        <p-column field="donarId" />
        <p-column field="donarName" />
        <p-column field="donarDOB" />
        <p-column field="donorPatientDoctor" />
        <p-column field="cycleId" />
        <p-column field="donatedToId" />
        <p-column field="donatedtoName" />
        <p-column field="donatedtoDOB" />
        <p-column field="donatedtoPatientDoctor" />
        <p-column field="dDate" />
        <p-column field="number" />
        <p-column field="donationType" />

        <template #footer>
            <p-button
                label="Create"
                class="p-mr-2 p-button-success"
                @click="create"
            />
            <p-button label="Edit" class="p-mr-2" @click="editSelectedRow" />
            <p-button
                label="Delete"
                class="p-mr-2 p-button-danger"
                @click="deleteThirdPartyConfirm"
            />

            <p-button
                label="PDF Reports"
                class="p-mr-2 p-button-secondary"
                @click="openPdfReportStandard"
            />
        </template>
    </p-data-table>
    <p-dialog
        v-model:visible="thirdPartyAddDialogue"
        modal
        :header="dialogMode === 'Add' ? 'Add' : 'Edit'"
        :style="{ width: '30rem' }"
    >
        <div>
            <div class="p-field p-grid" style="margin-bottom: 10px;">
                <label for="cycleId" class="p-col-4 p-mb-0">
                    Cycle ID
                </label>
                <div class="p-col">
                    <p-input-text v-model="newEntry.cycleId" :disabled="true" />
                </div>
            </div>
            <div class="p-field p-grid" style="margin-bottom: 10px;">
                <label for="firstname" class="p-col-4 p-mb-0">
                    Third Party Type
                </label>
                <div class="p-col">
                    <p-dropdown
                        v-model="newEntry.donationType"
                        style="width: 188px;"
                        id=""
                        :options="thirdPartyOpts"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
            </div>
            <div class="p-field p-grid" style="margin-bottom: 10px;">
                <label for="number" class="p-col-4 p-mb-0">
                    Number
                </label>
                <div class="p-col">
                    <p-input-text v-model="newEntry.number" />
                </div>
            </div>
            <div class="p-field p-grid" style="margin-bottom: 10px;">
                <label for="donatedTo" class="p-col-4 p-mb-0">
                    Donated To
                </label>
                <div class="p-col">
                    <p-auto-complete
                        v-model="newEntry.donatedToId"
                        :suggestions="donatedToOpts"
                        @complete="donatedToSuggestions($event)"
                        field="label"
                        optionLabel="label"
                        optionValue="demographicNo"
                    />
                </div>
            </div>
        </div>
        <div style="margin-top: 30px;">
            <p-button label="No" class="p-mr-2" @click="cancelDialogue" />
            <p-button
                label="Save"
                class="p-button-success"
                @click="saveEntry"
            />
        </div>
    </p-dialog>
    <p-dialog
        v-model:visible="deleteThirdPartyDialogue"
        modal
        header="Delete Third Party !!"
        :style="{ width: '30rem' }"
    >
        <p>
            Are you sure you want to delete?
        </p>
        <p-button label="No" class="p-mr-2" @click="cancelThirdPartyDialogue" />
        <p-button
            label="Yes"
            class="p-button-danger"
            @click="deleteThirdParty"
        />
    </p-dialog>
</template>

<script>
import { inject, ref } from "vue-demi";
import { $axios } from "@/service/app-http";
import Service from "@/service/art-cycle.js";
import { useConfirm } from "primevue/useconfirm";
import { useStore } from "vuex";
import { useToast } from "primevue/usetoast";
import { onMounted, onBeforeUnmount } from "vue";

export default {
    setup() {
        const store = useStore();
        const toast = useToast();
        const selectAllCheckbox = ref(false);
        const donatedTo = ref();
        const donatedToOpts = ref([]);
        const thirdPartyData = ref([]);

        const fetchThirdPartyData = async () => {
            if (store.state.artCycle.patient) {
                const patientId = store.state.artCycle.patient.id; // Replace with actual patient ID

                try {
                    const response = await $axios.get(
                        `third-party/list/${patientId}`
                    );
                    thirdPartyData.value = response.data.list;
                } catch (error) {
                    console.error("Error fetching third-party data:", error);
                }
            }
        };

        const selectedRow = ref(null);
        const checkList = ref([]);

        const findIndexById = (id) => {
            return thirdPartyData.value.findIndex((item) => item.id === id);
        };

        const onRowSelect = (event) => {
            selectedRow.value = { ...event.data };
        };

        const selectAllOnChange = () => {
            if (selectAllCheckbox.value) {
                checkList.value = thirdPartyData.value.map((item) => item.id);
            } else {
                checkList.value = [];
            }
        };

        const thirdPartyAddDialogue = ref(false);

        const newEntry = ref({
            dId: 0,
            cycleId: null,
            donatedToId: null,
            number: null,
            donationType: null
        });

        const cancelDialogue = () => {
            thirdPartyAddDialogue.value = false;
        };

        const create = () => {
            if (
                store.state.artCycle.selectedCycle &&
                store.state.artCycle.selectedCycle.cycleId
            ) {
                dialogMode.value = "Add";
                resetNewEntry();
                newEntry.value.cycleId =
                    store.state.artCycle.selectedCycle.cycleId;
                thirdPartyAddDialogue.value = true;
            } else {
                toast.add({
                    severity: "warn",
                    summary: "Error",
                    detail: "Please select cycle first",
                    life: 3000
                });
            }
        };

        const saveEntry = () => {
            console.log(newEntry.value);

            const payload = newEntry.value;

            if (
                payload.donatedToId &&
                typeof payload.donatedToId === "object" &&
                payload.donatedToId.demographicNo
            ) {
                payload.donatedToId = payload.donatedToId.demographicNo;
            }

            if (newEntry.value.dId === 0) {
                $axios.post(`third-party/add`, payload).then(() => {
                    fetchThirdPartyData();
                });
            } else {
                $axios
                    .put(`third-party/update/${newEntry.value.dId}`, payload)
                    .then(() => {
                        fetchThirdPartyData();
                    });
            }
            resetNewEntry();
            thirdPartyAddDialogue.value = false;
        };

        const resetNewEntry = () => {
            newEntry.value = {
                dId: 0,
                cycleId: null,
                donatedToId: null,
                number: null,
                donationType: null
            };
        };

        const isValidEntry = (entry) => {
            return true;
        };

        const editSelectedRow = () => {
            if (selectedRow.value) {
                newEntry.value = {
                    dId: selectedRow.value.dId,
                    cycleId: selectedRow.value.cycleId,
                    donatedToId: selectedRow.value.donatedToId,
                    number: selectedRow.value.number,
                    donationType: selectedRow.value.donationType
                };

                dialogMode.value = "Edit";
                thirdPartyAddDialogue.value = true;
            }
        };

        const deleteThirdPartyDialogue = ref(false);

        const deleteThirdPartyConfirm = () => {
            deleteThirdPartyDialogue.value = true;
        };

        const deleteThirdParty = () => {
            if (selectedRow.value) {
                const index = findIndexById(selectedRow.value.id);
                if (index !== -1) {
                    thirdPartyData.value.splice(index, 1);
                    selectedRow.value = null;
                }
            }

            deleteThirdPartyDialogue.value = false;
        };

        const cancelThirdPartyDialogue = () => {
            deleteThirdPartyDialogue.value = false;
        };

        const dialogMode = ref("Add");

        const donatedToSuggestions = (event) => {
            Service.searchDemographic({
                term: event.query.trim(),
                sex: "F".toLowerCase()
            }).then((resp) => {
                donatedToOpts.value = resp.data;
            });
        };

        const thirdPartyOpts = [
            { label: "Egg Donor", value: "eggDonor" },
            { label: "Embyro Donor", value: "embryoDonor" },
            { label: "Sperm Donor", value: "spermDonor" },
            { label: "Gestational Carrier", value: "gestationalCarrier" }
        ];

        const openPdfReportStandard = () => {};
        onMounted(() => {
            fetchThirdPartyData();
        });
        return {
            selectAllCheckbox,
            thirdPartyData,
            selectedRow,
            checkList,
            onRowSelect,
            openPdfReportStandard,
            thirdPartyAddDialogue,
            cancelDialogue,
            create,
            donatedToSuggestions,
            thirdPartyOpts,
            newEntry,
            saveEntry,
            resetNewEntry,
            isValidEntry,
            selectAllOnChange,
            editSelectedRow,
            dialogMode,
            deleteThirdParty,
            deleteThirdPartyDialogue,
            deleteThirdPartyConfirm,
            cancelThirdPartyDialogue,
            donatedToOpts,
            donatedTo,
            fetchThirdPartyData
        };
    }
};
</script>

<style scoped></style>
